<template>
	<div class="owl-carousel" :id="id">
		<slot></slot>
	</div>
</template>

<script>
import $ from 'jquery'
import '../../vendors/owl.carousel.jsm.js'
import EventBus from 'event-bus'

export default {
	props: {
		xs: {type: Number},
		sm: {type: Number},
		md: {type: Number},
		lg: {type: Number},
		xl: {type: Number},
		id: {type: String},
		margin: {
			default: 0,
			type: Number
		},
		loop: {
			type: Boolean,
			default: false
		},
		nav: {
			type: Boolean,
			default: false
		},
		dots: {
			type: Boolean,
			default: false
		},
		autoplay: {
			type: Boolean,
			default: false
		},
		mouseDrag: {
			type: Boolean,
			default: false
		}
	},
	data() {
		const responsive = {};

		responsive.xs = this.xs || 1;
		responsive.sm = this.sm || responsive.xs;
		responsive.md = this.md || responsive.sm;
		responsive.lg = this.lg || responsive.md;
		responsive.xl = this.xl || responsive.lg;

		return {
			responsive : responsive,
			$owl       : undefined
		}
	},
	methods: {
		create() {
			const $owl = this.$owl || $(this.$el);
			const childrenLenght = $owl.children().length;
			const {
				xs,
				sm,
				md,
				lg,
				xl
			} = this.responsive;

			// Criando a função que verifica de o nº de children é maior
			// a quantidade minima para habilitar o [loop]
			const hasLoop = (qnt) => {
				let enableLoop = childrenLenght > qnt;

				// Caso enableLoop for verdadeiro, retorna o valor da prop
				// [loop] caso o contrário retorna false
				return enableLoop ? this.loop : false;
			}


			// Construindo o carousel com os parametros
			$owl.owlCarousel({
				autoplay	: this.autoplay,
				nav			: this.nav,
				navText     : [
					// Caso queira alterar o conteúdi dos botões, altere aqui
					'<i class="fas fa-arrow-left"></i>',
					'<i class="fas fa-arrow-right"></i>'
				],
				dots		: this.dots,
				loop		: this.loop,
				margin      : this.margin,
				mouseDrag   : this.mouseDrag,
				responsive  : {
					0    : {
						items: xs,
						loop : hasLoop(xs)
					},
					576  : {
						items: sm,
						loop : hasLoop(sm)
					},
					768  : {
						items: md,
						loop : hasLoop(md)
					},
					992  : {
						items: lg,
						loop : hasLoop(lg)
					},
					1200 : {
						items: xl,
						loop : hasLoop(xl)
					},
				}
			});

			if(!this.$owl) this.$owl = $owl;
		},
		destroy() {
			if(this.$owl) this.$owl.trigger('destroy.owl.carousel');
		}

	},
	created(){

		// Escutando o evento de DESTROY
		EventBus.$on('destroy.owl.carousel', event => {

			if(event.id === this.id) {
				this.destroy();
			}

		});

		// Escutando o evento de CREATE
		EventBus.$on('create.owl.carousel', event => {

			if(event.id === this.id) {
				this.create();
			}

		});

	},
	mounted() {
		this.create();
	}
}
</script>

<style lang="scss">
/**
 * Owl Carousel v2.2.1
 * Copyright 2013-2017 Saurabh Sharma
 * Licensed under  ()
 */


/*
 *  Owl Carousel - Core
 */

.owl-carousel {
	display: none;
	width: 100%;
	-webkit-tap-highlight-color: transparent;
	/* position relative and z-index fix webkit rendering fonts issue */
	position: relative;
	z-index: 1;
}

.owl-carousel .owl-stage {
	position: relative;
	-ms-touch-action: pan-Y;
	-moz-backface-visibility: hidden;
	/* fix firefox animation glitch */
}

.owl-carousel .owl-stage:after {
	content: ".";
	display: block;
	clear: both;
	visibility: hidden;
	line-height: 0;
	height: 0;
}

.owl-carousel .owl-stage-outer {
	position: relative;
	overflow: hidden;
	/* fix for flashing background */
	-webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-wrapper,
.owl-carousel .owl-item {
	-webkit-backface-visibility: hidden;
	-moz-backface-visibility: hidden;
	-ms-backface-visibility: hidden;
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	-ms-transform: translate3d(0, 0, 0);
}

.owl-carousel .owl-item {
	position: relative;
	min-height: 1px;
	float: left;
	-webkit-backface-visibility: hidden;
	-webkit-tap-highlight-color: transparent;
	-webkit-touch-callout: none;
}

.owl-carousel .owl-item img {
	display: inline-block;
	max-width: 100%;
	height: auto;
}

.owl-carousel .owl-nav.disabled,
.owl-carousel .owl-dots.disabled {
	display: none;
}

.owl-carousel .owl-nav .owl-prev,
.owl-carousel .owl-nav .owl-next,
.owl-carousel .owl-dot {
	cursor: pointer;
	cursor: hand;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.owl-carousel.owl-loaded {
	display: block;
}

.owl-carousel.owl-loading {
	opacity: 0;
	display: block;
}

.owl-carousel.owl-hidden {
	opacity: 0;
}

.owl-carousel.owl-refresh .owl-item {
	visibility: hidden;
}

.owl-carousel.owl-drag .owl-item {
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}

.owl-carousel.owl-grab {
	cursor: move;
	cursor: grab;
}

.owl-carousel.owl-rtl {
	direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
	float: right;
}


/* No Js */

.no-js .owl-carousel {
	display: block;
}


/*
 *  Owl Carousel - Animate Plugin
 */

.owl-carousel .animated {
	animation-duration: 1000ms;
	animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
	z-index: 0;
}

.owl-carousel .owl-animated-out {
	z-index: 1;
}

.owl-carousel .fadeOut {
	animation-name: fadeOut;
}

@keyframes fadeOut {
	0% {
		opacity: 1;
	}
	100% {
		opacity: 0;
	}
}


/*
 * 	Owl Carousel - Auto Height Plugin
 */

.owl-height {
	transition: height 500ms ease-in-out;
}


/*
 * 	Owl Carousel - Lazy Load Plugin
 */

.owl-carousel .owl-item .owl-lazy {
	opacity: 0;
	transition: opacity 400ms ease;
}

.owl-carousel .owl-item img.owl-lazy {
	transform-style: preserve-3d;
}


/*
 * 	Owl Carousel - Video Plugin
 */

.owl-carousel .owl-video-wrapper {
	position: relative;
	height: 100%;
	background: #000;
}

.owl-carousel .owl-video-play-icon {
	position: absolute;
	height: 80px;
	width: 80px;
	left: 50%;
	top: 50%;
	margin-left: -40px;
	margin-top: -40px;
	background: url('data:image/png;base64,') no-repeat;
	cursor: pointer;
	z-index: 1;
	-webkit-backface-visibility: hidden;
	transition: transform 100ms ease;
}

.owl-carousel .owl-video-play-icon:hover {
	-ms-transform: scale(1.3, 1.3);
	transform: scale(1.3, 1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
	display: none;
}

.owl-carousel .owl-video-tn {
	opacity: 0;
	height: 100%;
	background-position: center center;
	background-repeat: no-repeat;
	background-size: contain;
	transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
	position: relative;
	z-index: 1;
	height: 100%;
	width: 100%;
}
</style>
