<template lang="pug">
	.v__zoom
		img(
			id="zoom__image"
			:src="imgActive.md"
			:data-zoom-image="imgActive.xl"
			loading="lazy"
			width="540"
			height="510"
			:alt="imgActive.alt"
		).img-fluid

		//- h3 OUTROS ÂNGULOS
		owl-carousel(
			nav
			:xs="5"
			:margin="15"
		).v__zoom__galery#v__galery
			a(
				v-for="img in album"
				:key="img.id"
				href="#"
				:data-image="img.md"
				:data-zoom-image="img.xl"
				:class=`{
					'active': img.active
				}`
				@click="setActive(img)"
			)
				img(
					:src="img.xs"
					width="100"
					height="100"
					loading="lazy"
					:alt="img.alt"
				).img-fluid
		//- .v__zoom__galery#galery
	//- .v__zoom
</template>

<script>
export default {
	props: {
		fotos: Array
	},
	data() {
		var imgActive = this.fotos.find(item => item.active === true);

		return {
			album: this.fotos,
			imgActive: imgActive
		}
	},
	methods: {
		setActive(img) {
			this.album.map(foto => {
				foto.active = img.id === foto.id;
			})
		}
	},
	mounted() {
		var config  = {
			gallery: 'v__galery',
			cursor: 'crosshair',
			galleryActiveClass: 'active',
			imageCrossfade: true,
			responsive: true,
			loadingIcon: 'data:image/svg+xml;base64,'
		};

		$('#zoom__image').elevateZoom(config);
	}
}
</script>
